.carousel-indicators li {
    display: inline-block;
    width: 10px;
    height: 10px;
    margin: 4px;
    text-indent: -999px;
    cursor: pointer;
    background-color: #000000 \9;
    border: 1px solid #fff;
    border-radius: 2px;
        @media (max-width: $screen-xs-min) {
        width: 5px;
        height: 5px;
        border-radius: 1px;
        }
}
.carousel-indicators .active {
    width: 12px;
    height: 12px;
    margin: 3px;
    background-color: #FFFFFF;
    @media (max-width: $screen-xs-min) {
        width: 7px;
        height: 7px;
    }
}
.carousel-control.left {
    background-image: none;
    filter: none;
}
.carousel-control.right {
    background-image: none;
    filter: none;
}

.carousel-caption {
    opacity: 0;
    font-size: 1.5em;
    color:#F8F8F8;
    margin-bottom: 1%;
    @media (max-width: $screen-xs-max) {
        font-size: 1.25em;
    }
    @media (max-width: $screen-xs-min) {
        font-size: 1em;
    }
}
.glyphicon-menu-down {
    font-size: 2em;
    padding-top: 0;
    color: #000000;
    @media (max-width: $screen-xs-max) {
        font-size: 1.5em;
    }
}
.container-arrow {
    cursor: pointer;
    text-align: center;
    padding: 15px 0;
    p {
        font-size: 1.5em;
        margin: 0;
    }
    @media (max-width: $screen-xs-min) {
        p {font-size: 1em;}
        padding: 7px 0;
    }
}
.navbar-brand {
    margin: 5px 0;
    padding: 0;
    img {
        height: 50px;
    }
}
.form-control {
    border-radius: 0;
    -webkit-box-shadow: none;
    box-shadow: none;
    -webkit-transition: none;
    -o-transition: none;
    transition: none;
}
.form-control:focus {
    border-color: #000000;
    outline: 0;
    -webkit-box-shadow: none;
    box-shadow: none;
}
.form-inline {display: none;}
.forminput {
    padding: 15px 15px 30px 15px;
    #input-1 {
        width: 100%;
    }
    @media (max-width: $screen-xs-max) {
        padding: 10px 40px;
    }
}
.butinput {
    padding: 15px 15px 30px 0;
    @media (max-width: $screen-xs-max) {
        padding: 10px 40px;
    }
}
.randlpad {
    padding-left: 0;
    padding-right: 0;
}
#btn-nav-1 {
    width: 100%;
    @media (max-width: $screen-xs-max) {
        margin: 20px 0;
    }
}
.navbar-right {
    @media (max-width: $screen-xs-max) {
        width: 100%;
        margin: 0 auto;
    }
}