// PT Sans Regular
@font-face {
    font-family: "PT Sans";
    src:
        local('PT Sans'),
        local('PTSans'),
        local('PT-Sans'),
        local('PT_Sans'),
        url(fonts/PT_Sans-Web-Regular.ttf) format('truetype');
    font-weight: 400;
    font-style: normal;
}
// PT Sans Italic
@font-face {
    font-family: "PT Sans";
    src:
        local('PT Sans Italic'),
        local('PTSans Italic'),
        local('PTSans-Italic'),
        local('PT-Sans-Italic'),
        local('PT_Sans_Italic'),
        url(fonts/PT_Sans-Web-Italic.ttf) format('truetype');
    font-weight: 400;
    font-style: italic;
}
// PT Sans Bold
@font-face {
    font-family: "PT Sans";
    src:
        local('PT Sans Bold'),
        local('PTSans Bold'),
        local('PTSans-Bold'),
        local('PT-Sans-Bold'),
        local('PT_Sans_Bold'),
        url(fonts/PT_Sans-Web-Bold.ttf) format('truetype');
    font-weight: 700;
    font-style: normal;
}
// PT Sans Bold Italic
@font-face {
    font-family: "PT Sans";
    src:
        local('PT Sans Bold Italic'),
        local('PTSans BoldItalic'),
        local('PTSansBoldItalic'),
        local('PTSans-BoldItalic'),
        local('PT-Sans-BoldItalic'),
        local('PT_Sans_BoldItalic'),
        url(fonts/PT_Sans-Web-BoldItalic.ttf) format('truetype');
    font-weight: 700;
    font-style: italic;
}