$nav-bg-default     : rgba(255, 255, 255, 0);
$nav-bg-highlight   : rgba(249, 249, 249, 1);
$nav-color-default  : #000000;
$nav-color-highlight: #000000;
$nav-dropDown       : false;

$nav-inv-bg-default     : #3C3C3C;
$nav-inv-bg-highlight   : #3C3C3C;
$nav-inv-color-default  : #DDDDDD;
$nav-inv-color-highlight: #FFFFFF;
$nav-inv-dropDown       : false;

$nav-default-font-size: 1.2em;
$nav-brand-font-size  : 1.5em;
$nav-brand-font-family: Georgia, Times, Times New Roman,serif;
$nav-padding-top      : 20px;

.navbar-default {
    background-color: $nav-bg-default;
    border-color: $nav-bg-highlight;
    .navbar-brand {
        color: $nav-color-default;
        &:hover, &:focus {color: $nav-color-highlight;}
    }
    .navbar-text {color: $nav-color-default;}
    .navbar-nav {
        > li {
            > a {color: $nav-color-default; &:hover, &:focus {color: $nav-color-highlight; text-decoration: underline;}}
            @if $nav-dropDown {
                > .dropdown-menu {
                  background-color: $nav-bg-default;
                  > li > a {
                    color: $nav-color-default;
                    &:hover,  &:focus {color: $nav-color-highlight; background-color: $nav-bg-highlight;}
                  }
                  .divider {background-color: $nav-bg-highlight;}
                }
            }
        }
        > .active {
            > a, > a:hover, > a:focus {color: $nav-color-highlight; background-color: $nav-bg-highlight;}
        }
        > .open {
            > a, > a:hover, > a:focus {color: $nav-color-highlight; background-color: $nav-bg-highlight;}
        }
    }
    .navbar-toggle {
        border-color: $nav-bg-highlight;
        &:hover, &:focus {
            background-color: $nav-bg-highlight;
        }
        .icon-bar {background-color: $nav-color-default;}
    }
    .navbar-collapse, .navbar-form {border-color: $nav-color-default;}
    .navbar-link {
        color: $nav-color-default;
        &:hover {color: $nav-color-highlight;}
    }
}
@media (max-width: $screen-xs-max) {
    .navbar-default .navbar-nav .open .dropdown-menu {
        > li > a {
          color: $nav-color-default;
          &:hover, &:focus {color: $nav-color-highlight;}
        }
        > .active {
          > a, > a:hover, > a:focus {color: $nav-color-highlight; background-color: $nav-bg-highlight;}
        }
    }
}
.navbar-inverse {
    background-color: $nav-inv-bg-default;
    border-color: $nav-inv-bg-highlight;
    .navbar-brand {
        color: $nav-inv-color-default;
        &:hover, &:focus {color: $nav-inv-color-highlight;}
    }
    .navbar-text {color: $nav-inv-color-default;}
    .navbar-nav {
        > li {
            > a {color: $nav-inv-color-default; &:hover, &:focus {color: $nav-inv-color-highlight;}}
            @if $nav-inv-dropDown {
                > .dropdown-menu {
                    background-color: $nav-inv-bg-default;
                    > li > a {
                        color: $nav-inv-color-default;
                        &:hover,  &:focus {color: $nav-inv-color-highlight; background-color: $nav-inv-bg-highlight;}
                    }
                    .divider {background-color: $nav-inv-bg-highlight;}
                }
            }
        }
        > .active {
            > a, > a:hover, > a:focus {color: $nav-inv-color-highlight; background-color: $nav-inv-bg-highlight;}
        }
        > .open {
            > a, > a:hover, > a:focus {color: $nav-inv-color-highlight; background-color: $nav-inv-bg-highlight;}
        }
    }
    .navbar-toggle {
        border-color: $nav-inv-bg-highlight;
        &:hover, &:focus {background-color: $nav-inv-bg-highlight;}
        .icon-bar {background-color: $nav-inv-color-default;}
    }
    .navbar-collapse, .navbar-form {border-color: $nav-inv-color-default;}
    .navbar-link {
        color: $nav-inv-color-default;
        &:hover {color: $nav-inv-color-highlight;}
    }
}
@media (max-width: $screen-xs-max) {
    .navbar-default .navbar-nav .open .dropdown-menu {
        > li > a {
            color: $nav-inv-color-default;
            &:hover, &:focus {color: $nav-inv-color-highlight;}
        }
        > .active {
            > a, > a:hover, > a:focus {color: $nav-inv-color-highlight; background-color: $nav-inv-bg-highlight;}
        }
    }
}
.navbar-form {
    padding: 15px;
    margin: 0;
    margin-bottom: 16px;
    border: none;
    @media (max-width: $screen-sm-min){
        margin-bottom: 0;
        margin-top: 0;
        padding: 0 30px;
        padding-bottom: 20px;
    }
}
.navbar-default {
    font-size:$nav-default-font-size !important;
    margin-bottom: 0;
    font-size: $nav-default-font-size;
    .navbar-brand {
        font-size: $nav-brand-font-size;
    }
    .navbar-nav {

        > li {
            > a  {
                &:hover, &:focus{ text-decoration: underline;}}}}}
#btn-nav-1 {
    @media (max-width: $screen-sm-min){
        width: 100%;
    }
}

