.fot-3-col {
    margin-bottom: 15px;
    background: #F8F8F8;
}
.ftext {
    margin: 0 auto;
    text-align: center;
    width: 80%;
    h4 {
        padding-top: 15px;
        text-transform: uppercase;
    }
    p {
        color: #929291;
        padding-top: 15px;
        padding-bottom: 45px;
    }
}
.btn-sm {width: 80%;}
.btnalg {
    text-align:center;
    padding-bottom: 45px;
}
.fcontacts {
    height: 90px;
    text-align: left;
    width: 80%;
    margin: 0 auto;
        p {
            color: #929291;
            margin-bottom: 0;
        }
    @media (max-width: $screen-xs-max) {
        text-align: center;
    }
    table {width: 100%;}
    td {width: 50%;}
}
.follow {
    height: 90px;
    padding: 0;
    text-align: center;
    ul {
        padding-left: 0;
    }
    ul li {
        display: inline;
        font-size: 2em;
        margin-right: 30px;
        &:nth-last-child(1) {
            margin-right: 0;
        }
        @media (max-width: $screen-xs-max) {
            font-size: 2.5em;
        }
        a {
            cursor: pointer;
            color: #929291;
            text-decoration: none;
            &:hover,
            &:focus {
                color: #000000;
            }
            &:focus {
                outline: thin dotted;
                outline: 5px auto -webkit-focus-ring-color;
                outline-offset: -2px;
            }
        }

        h4 {
            @media (max-width: $screen-xs-max) {
                margin-top: 0;
            }
        }
    }
}
.botrow {
    text-align: center;
    height: 30px;
    background-color: #F8F8F8;
        p {
            color: #000000;
            margin-bottom: 0;
            line-height: 30px;
        }
}
.anchor {
    cursor: pointer;
    height: 60px;
    width: 80%;
    margin: 15px auto auto auto;
    border: 3px solid #F8F8F8;
    text-align: center;
    font-size: 3em;
    @media (max-width: $screen-xs-max) {
        margin: 15px auto 30px auto;
    }
    &:hover, &focus {background: #F8F8F8;
    border: 1px solid black }
}
.minfo {
    text-align: right;
    span {font-size: 3em;}
    a {
        cursor: pointer;
        color: #929291;
        text-decoration: none;
        &:hover,
        &:focus {
            color: #000000;
        }
        &:focus {
            outline: thin dotted;
            outline: 5px auto -webkit-focus-ring-color;
            outline-offset: -2px;
        }
    }
}
