$btn-df-color                       : #333333;
$btn-df-bg-color                    : #FFFFFF;
$btn-df-border-color                : #000000;

$btn-df-hover-color                 : #333333;
$btn-df-hover-bg-color              : #FFFFFF;
$btn-df-hover-border-color          : #000000;

$btn-df-active-color                : #333333;
$btn-df-active-bg-color             : #FFFFFF;
$btn-df-active-border-color         : #000000;

$btn-df-active-hover-color          : #333333;
$btn-df-active-hover-bg-color       : #FFFFFF;
$btn-df-active-hover-border-color   : #000000;

$btn-df-disabled-bg-color           : #FFFFFF;
$btn-df-disabled-border-color       : #FF0000;

$btn-df-badge-color                 : #000000;
$btn-df-badge-bg-color              : #FFFFFF;

.btn-default {
    color: $btn-df-color;
    background-color: $btn-df-bg-color;
    border-color: $btn-df-border-color;
}
.btn-default:focus,
.btn-default.focus {
    text-decoration: underline;
    color: $btn-df-hover-color;
    background-color: $btn-df-hover-bg-color;
    border-color: $btn-df-hover-border-color;
}
.btn-default:hover {
    text-decoration: underline;
    color:  $btn-df-hover-color;
    background-color: $btn-df-hover-bg-color;;
    border-color: $btn-df-hover-border-color;
}
.btn-default:active,
.btn-default.active,
.open > .dropdown-toggle.btn-default {
    color: $btn-df-active-color;
    background-color: $btn-df-active-bg-color;
    border-color: $btn-df-active-border-color;
}
.btn-default:active:hover,
.btn-default.active:hover,
.open > .dropdown-toggle.btn-default:hover,
.btn-default:active:focus,
.btn-default.active:focus,
.open > .dropdown-toggle.btn-default:focus,
.btn-default:active.focus,
.btn-default.active.focus,
.open > .dropdown-toggle.btn-default.focus {
    color: $btn-df-active-hover-color;
    background-color: $btn-df-active-hover-bg-color;
    border-color: $btn-df-active-hover-border-color;
}
.btn-default:active,
.btn-default.active,
.open > .dropdown-toggle.btn-default {
    background-image: none;
}
.btn-default.disabled,
.btn-default[disabled],
fieldset[disabled] .btn-default,
.btn-default.disabled:hover,
.btn-default[disabled]:hover,
fieldset[disabled] .btn-default:hover,
.btn-default.disabled:focus,
.btn-default[disabled]:focus,
fieldset[disabled] .btn-default:focus,
.btn-default.disabled.focus,
.btn-default[disabled].focus,
fieldset[disabled] .btn-default.focus,
.btn-default.disabled:active,
.btn-default[disabled]:active,
fieldset[disabled] .btn-default:active,
.btn-default.disabled.active,
.btn-default[disabled].active,
fieldset[disabled] .btn-default.active {
    background-color: $btn-df-disabled-bg-color ;
    border-color: $btn-df-disabled-border-color;
}
.btn-default .badge {
    color: $btn-df-badge-color;
    background-color: $btn-df-badge-bg-color;
}