$text-size-tile: 1.2em;
.row {margin: 0} // АХТУНГ! этот клас режет маржины у всех строк на странице
.tile {
    padding: 0;
    position: relative;
    margin-bottom: 15px;
    text-align: justify;
    height: 700px;
    background: #F8F8F8;
    img {padding-bottom: 15px;}
    span {font-size: $text-size-tile;}
    p {
        font-size: $text-size-tile;
        margin: 1.5em 0;
    }
    a {
        font-size: $text-size-tile;
        cursor: pointer;
    }
}
.small-tile {
    img:first-child {
        position: absolute;
        top: 0;
    }
    @media (max-width: $screen-xs-max) {
        border-right: none;
    }
    @media (max-width: $screen-xs-min) {
        height: 500px;
        border-right: none;
    }
}
.big-tile  {
    img:first-child {
        position: absolute;
        top: 0;
    }
    padding-left: 0;
    padding-right: 0;
    @media (max-width: $screen-xs-max) {
        border: none;
        margin: 0;
    }
    @media (max-width: $screen-xs-min) {
        height: 500px;
        border: none;
    }
}
.table-bot {
    position: absolute;
    bottom: 1em;
}
.tile-text {margin: 15px;}
/*Первый блок с плитками.Малая плитка слева, большая-справа.Граница между ними 15px.*/
.t-sm-1 {
    border-right: 10px solid white;
    @media (max-width: $screen-xs-max) {
        border: none;
        margin: 0;
    }
}
.t-big-1 {
    border-left: 5px solid white;
}
/*Второй блок с плитками.Три малые плитки.Граница между ними 15px.*/
.t-sm-2 {
    border-right: 10px solid white;
    @media (max-width: $screen-xs-max) {
        border: none;
        margin: 0;
    }
}
.t-sm-3 {
    border-left: 5px solid white;
    border-right: 5px solid white;
    @media (max-width: $screen-xs-max) {
        border: none;
        margin: 0;
    }
}
.t-sm-4 {
    border-left: 10px solid white;
    @media (max-width: $screen-xs-max) {
        border: none;
        margin: 0;
    }
}
/*Третий блок с плитками. Большая плитка слева, малая-справа.Граница между ними 15px.*/
.t-big-2 {
    border-right: 5px solid white;
    margin-bottom: 0;
    @media (max-width: $screen-xs-max) {
        border: none;
        margin: 0;
    }
}
.t-sm-5 {
    border-left: 10px solid white;
    margin-bottom: 0;
    @media (max-width: $screen-xs-max) {
        border: none;
        margin: 0;
    }
}
.see-more {
    cursor: pointer;
    text-align: center;
    padding: 15px 0;
    p {
        font-size: 1.5em;
        margin: 0;
    }
    @media (max-width: $screen-xs-min) {
        p {font-size: 1em;}
        padding: 7px 0;
    }
    span {margin: 5px;}
}
.space {
    width: 100%;
    float: left;
    height: 15px;
    @media (max-width: $screen-xs-min) {height: 0;}
    &.space-top {
        height: 15px;
        @media (max-width: $screen-xs-min) {height: 0;}
    }
    &.space-bot {height: 45px;}
}
